import React from 'react';
import './Button.scss';

const Button = ({txt,bgColor,width, btnWidth, lSpacing, txtSize, fWeight, colorTxt, padding,txtTransform, margin, link}) => {

    console.log(colorTxt);

    const styleBtn = {
        color: `${colorTxt}`,
        backgroundColor: `${bgColor}`,
        width: `${btnWidth}`,
        minwidth: `${width}`,
        maxWidth: '100%',
        fontSize: `${txtSize}`,
        padding: `${padding}`,
        textTransform: `${txtTransform}`,
        marginTop: `${margin}`,
        textAlign: 'center',
        borderRadius: '100px',
        cursor: 'pointer',
        textDecoration: "none",
        fontWeight: `${fWeight}`,
        letterSpacing: `${lSpacing}`
    }
    return (
        <div className="btn" style={styleBtn}>
                <a className="td-none white" href={link} target="_blank" rel="noopener"npm>{txt}</a>
        </div>
    )
}

export default Button;